import { React } from 'react';
import './TeamTile.css';
import { Link } from 'react-router-dom';

export const TeamTile = (props) => {
    const { teamName } = props;
    return (
        <div className="TeamTile">
            <h1 className = "team-name">
                <Link to={`/teams/${teamName}`}>{teamName}</Link>
            </h1>
        </div>
    );
}