
import { React, useEffect, useState } from 'react';
import { MatchDetailCard } from '../components/MatchDetailCard';
import { useParams } from 'react-router-dom';
import './MatchPage.css';
import { YearSelector } from '../components/YearSelector';
export const MatchPage = () => {

    const [matches, setMatches] = useState([]);

    const { pathTeamName, year } = useParams();

    useEffect(
        () => {
            const fetchMatches = async () => {
                const response = await fetch(`${process.env.REACT_APP_API_ROOT_URL}/team/${pathTeamName.trim()}/matches?year=${year}`);
                const data = await response.json();
                console.log(data);
                setMatches(data);
            }
            fetchMatches();
        },
        [year, pathTeamName]
    );
    return (
        <div className="MatchPage">
            <div className="year-selector">
                <h3>{'Select Year'}</h3>
                <YearSelector
                    teamName={pathTeamName}
                />
            </div>
            <div className="sub-list">
                <h1 className="page-heading">{`${pathTeamName} matches in ${year}`}</h1>
                {matches.map(match =>
                    <MatchDetailCard
                        teamName={pathTeamName}
                        match={match}
                        key={match.id}
                    />)}
            </div>
        </div>
    );
}

