
import { React } from 'react';
import { Link } from 'react-router-dom';
import './MatchDetailCard.css';
export const MatchDetailCard = (props) => {
    const { match, teamName } = props;
    const { team1 = '', team2 = '', date = '', venue = '', matchWinner = '',
        resultMargin = '', result = '', playerOfMatch = '', umpire1 = '', umpire2 = '' } = match;
    const otherTeam = team1 === teamName ? team2 : team1;
    const otherTeamRoute = `/teams/ ${otherTeam}`;
    const isMatchWon = teamName === matchWinner;
    return (
        <div className={isMatchWon ? 'MatchDetailCard won-card' : 'MatchDetailCard lost-card'}>
            <div>
                <span className="vs">vs</span>
                <h1><Link to={otherTeamRoute}>{otherTeam}</Link></h1>
                <h2 className="match-date">{date}</h2>
                <h3 className="match-venue">{venue}</h3>
                <h3 className="match-result">{matchWinner} won by {resultMargin} {result}</h3>
            </div>
            <div className="additional-detail">
                <h3>{'First innings'}</h3>
                <p className="additional-team1">{team1}</p>
                <h3>{'Second innings'}</h3>
                <p className="additional-team2">{team2}</p>
                <h3>{'Man of the match'}</h3>
                <p className="additional-match-player">{playerOfMatch}</p>
                <h3>{'Umpires'}</h3>
                <p className="additional-umpires">{umpire1},{umpire2}</p>
            </div>
        </div>
    );
}

