import './App.css';
import { TeamPage } from './pages/TeamPage';
import { MatchPage } from './pages/MatchPage';
import { HomePage } from './pages/HomePage';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route
            path="/teams/:pathTeamName/matches/:year"
          >
            <MatchPage />
          </Route>
          <Route
            path="/teams/:pathTeamName"
          >
            <TeamPage />
          </Route>
          <Route
            path="/"
          >
            <HomePage />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
