
import { React } from 'react';
import { Link } from 'react-router-dom';
import './MatchSmallCard.css';

export const MatchSmallCard = (props) => {
    const { match, teamName } = props;
    const { team1, team2, matchWinner, resultMargin, result } = match;
    const otherTeam = team1 === teamName ? team2 : team1;
    const otherTeamRoute = `/teams/ ${otherTeam}`;
    const isMatchWon = teamName === matchWinner;
    return (
        <div className={isMatchWon ? 'MatchSmallCard won-card' : 'MatchSmallCard lost-card'}>
            <span className="vs">vs</span>
            <h1><Link to={otherTeamRoute}>{otherTeam}</Link></h1>
            <p className = "match-result">{matchWinner} won by {resultMargin} {result}</p>
        </div>
    );
}

