
import { React, useEffect, useState } from 'react';
import { MatchDetailCard } from '../components/MatchDetailCard';
import { MatchSmallCard } from '../components/MatchSmallCard';
import { useParams, Link } from 'react-router-dom';
import './TeamPage.css';
import { PieChart } from 'react-minimal-pie-chart';

export const TeamPage = () => {

    const [team, setTeam] = useState({});
    const { pathTeamName } = useParams();
    console.log(pathTeamName);
    useEffect(
        () => {
            const fetchMatches = async () => {
                const response = await fetch(`${process.env.REACT_APP_API_ROOT_URL}/team/${pathTeamName.trim()}`);
                const data = await response.json();
                console.log(data);
                setTeam(data);
            }
            fetchMatches();
        },
        [pathTeamName]
    );

    const { teamName = '', matches = [], totalWins, totalMatches } = team;

    if (teamName === '') {
        return (
            <h1>{'Team not available'}</h1>
        )
    }
    return (
        <div className="TeamPage">
            <div className="team-name-section">
                <h1 className="team-name">{teamName}</h1>
            </div>
            <div className="win-loss-section">
                {'Wins/Losses'}
                <PieChart
                    data={[
                        { title: 'Wins', value: totalWins, color: '#E38627' },
                        { title: 'Losses', value: totalMatches - totalWins, color: '#C13C37' }
                    ]}
                />;
            </div>
            {matches.length > 0 &&
                <div className="match-detail-section">
                    <h3>{'Latest Matches'}</h3>
                    <MatchDetailCard
                        teamName={teamName}
                        match={matches[0]}
                    />
                </div>
            }

            {matches.slice(1).map(match =>
                <MatchSmallCard
                    teamName={teamName}
                    match={match}
                    key={match.id}
                />)}
            <div className="more-details">
                <Link to={`/teams/${teamName}/matches/${process.env.REACT_APP_DATA_END_YEAR}`}>{'More >'}</Link>
            </div>
        </div>
    );
}

